<template>
  <v-container fluid>
    <v-row no-gutters>
      <v-col cols="12">
        <h1 class="black--text font-weight-light">测试数据查询</h1>
      </v-col>
      <v-col cols="4">
        <v-text-field
          label="Input Serial Number"
          autosize
          autofocus
          outlined
          clearable
          v-model="sernum"
          v-on:keyup.enter="getTestData()"
        >
        </v-text-field>
      </v-col>
      <v-col cols="4">
        <v-btn style="margin-left: 10px; margin-top: 5px;"
        large 
        color="primary"
        @click="getTestData()"
        >
        Search</v-btn>
      </v-col>
      <v-col cols="4">
        <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Filter"
        single-line
        hide-details
      ></v-text-field>
      </v-col>
      <v-col cols="12">
        <v-data-table
          :headers="headers"
          :items="desserts"
          item-key="name"
          class="elevation-1"
          :search="search"
          :loading="loading"
          loading-text="Loading... Please wait"
        ></v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { KunlunApi } from '../api'
import store from '../store'

export default {
  name: 'TestData',
  components: {
  },
  data: () => ({
    sernum: '',
    loading: false,
    search: '',
    headers: [
      {
        text: 'RECORD TIME',
        align: 'start',
        sortable: false,
        value: 'name',
      },
      { text: 'SERIAL', value: 'calories' },
      { text: 'UUTTYPE', value: 'fat' },
      { text: 'RESULT', value: 'carbs' },
      { text: 'MACHINE', value: 'protein' },
      { text: 'CONTAINER', value: 'iron' },
    ],
    desserts: [
      {
        name: 'Frozen Yogurt',
        calories: 159,
        fat: 6.0,
        carbs: 24,
        protein: 4.0,
        iron: '1%',
      },
      {
        name: 'Ice cream sandwich',
        calories: 237,
        fat: 9.0,
        carbs: 37,
        protein: 4.3,
        iron: '1%',
      },
      {
        name: 'Eclair',
        calories: 262,
        fat: 16.0,
        carbs: 23,
        protein: 6.0,
        iron: '7%',
      }
    ]
  }),
  mounted () {
  },
  beforeDestroy () {
  },
  methods: {
    getTestData () {
      KunlunApi({ service: 'get-test-data', sernum: this.sernum })
        .then(response => {
          this.desserts = response.data.data
          store.commit('setMessage', response.data.message) 
        })
        .catch(e => {
          console.log(e)
        })
    },
  }
}
</script>
<style scoped>

</style>